.list-signup__container {
  @include smooth-text;
  background: $color--venice-blue url(#{$path}/images/list-signup.jpg) no-repeat
    center top;
  background-size: cover;
  min-height: 40rem;
  padding-bottom: unit(1);
  padding-top: unit(12);
  position: relative;
  text-align: center;

  @media (min-resolution: 120dpi) and (max-width: map-get($breakpoints, 'small--down')) {
    background-image: url(#{$path}/images/list-signup@2x.jpg);
  }

  @include respond-to('small') {
    background-image: url(#{$path}/images/list-signup--small.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'small')) {
    background-image: url(#{$path}/images/list-signup--small@2x.jpg);
  }

  @include respond-to('medium-large') {
    background-image: url(#{$path}/images/list-signup--medium.jpg);
    background-position: center;
    min-height: 25rem;
    padding-bottom: unit(2);
    padding-top: unit(2);
    text-align: left;
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'medium-large')) {
    background-image: url(#{$path}/images/list-signup--medium@2x.jpg);
  }

  @include respond-to('large') {
    background-image: url(#{$path}/images/list-signup--large.jpg);
  }

  @media (min-resolution: 120dpi) and (min-width: map-get($breakpoints, 'large')) {
    background-image: url(#{$path}/images/list-signup--large@2x.jpg);
  }

  &::after {
    @include pseudo;
    background: linear-gradient(
      to top,
      $color--venice-blue 40%,
      rgba($color--venice-blue, 0) 100%
    );
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;

    @include respond-to('medium-large') {
      background: linear-gradient(
        to left,
        $color--venice-blue 40%,
        rgba($color--venice-blue, 0) 100%
      );
      left: 25%;
    }
  }
}

.list-signup {
  max-width: 480px;
  position: relative;

  @media (max-width: map-get($breakpoints, 'medium-large--down')) {
    margin: 0 auto;
  }
}

.list-signup__newsletter {
  font-size: 1.3rem;
  font-weight: $font-weight--bold;
  line-height: unit(1);
  margin-top: unit(-1);
  position: relative;
  text-align: left;
  z-index: 3;

  @include respond-to('medium-large') {
    margin-left: 0;
  }

  a {
    @include hover-underline;
    color: $color--white;
  }
}

/*
 * constant contact brute-force styles
 */

.g-recaptcha {
  display: none !important;
}

.ctct-inline-form {
  background-color: transparent !important;
  margin: 0 auto !important;
  max-width: 480px !important;
  padding-bottom: unit(1.5) !important;
  position: static !important;
  z-index: 3 !important;
}

.ctct-form-defaults {
  background-color: transparent !important;
  padding: 0 !important;
}

.ctct-form-header {
  color: $color--white !important;
  font: $font-weight--normal 2.4rem/1.4 $font-family--sans !important;
  margin-bottom: unit(0.5) !important;
}

.ctct-form-text,
.ctct-gdpr-text {
  color: $color--white !important;
  font: $font-weight--normal 1.3rem/1.75 $font-family--sans !important;
  margin: unit(0.5) 0 !important;
}

.ctct-gdpr-text {
  @include smooth-text;
  font-size: 1rem !important;

  a {
    border-bottom: 1px solid rgba(#fff, 0.5) !important;
    color: #fff !important;
    text-decoration: none !important;
  }
}

.ctct-form-custom {
  @include un-smooth-text;
  padding-bottom: unit(2.5) !important;
  position: relative !important;
  z-index: 3 !important;

  @include respond-to('medium-large') {
    margin-left: 0 !important;
  }
}

.ctct-form-field {
  bottom: 0 !important;
  height: unit(2) !important;
  margin: 0 !important;
  overflow: hidden !important;
  position: absolute !important;
  width: 100% !important;
}

.ctct-form-label {
  color: $color--venice-blue !important;
  font: $font-weight--bold 1.3rem/4rem $font-family--sans !important;
  left: unit(0.5) !important;
  line-height: unit(2) !important;
  position: absolute !important;
  top: 0 !important;
  z-index: 3 !important;

  &::before {
    display: none !important;
  }
}

.ctct-form-custom[label-is='floated'] .ctct-form-label {
  top: unit(-2) !important;
}

.ctct-form-element {
  appearance: none !important;
  background-color: $color--white !important;
  border: none !important;
  border-radius: 2px !important;
  bottom: 0 !important;
  box-shadow: inset 1px 2px 3px rgba($color--black, 0.2) !important;
  color: $color--tuatara !important;
  font-size: 1.6rem !important;
  height: unit(2) !important;
  left: 0 !important;
  margin: 0 !important;
  padding: 0 unit(5) 0 unit(0.5) !important;
  position: absolute !important;
  width: 100% !important;
}

.ctct-form-button {
  background: transparent !important;
  border: none !important;
  border-radius: 0 !important;
  bottom: unit(0.5) !important;
  color: $color--rose-of-sharon !important;
  font: $font-weight--bold 1.4rem/2rem $font-family--sans !important;
  font-size: 1.4rem !important;
  padding: 0 14px 0 0 !important;
  position: absolute !important;
  right: unit(0.5) !important;
  width: auto !important;
  z-index: 3 !important;

  &::after {
    @include pseudo;
    background: url(#{$path}/images/arrow-link--orange.svg) no-repeat center;
    height: 12px;
    margin-top: -6px;
    right: 0;
    top: 50%;
    width: 7px;
  }
}

.ctct-form-element,
.ctct-form-button {
  &:focus {
    box-shadow: 0 0 0 3px $color--yellow-sea !important;
    outline: none !important;
  }

  [data-whatintent='mouse'] &:active,
  [data-whatintent='mouse'] &:focus,
  [data-whatintent='touch'] &:active,
  [data-whatintent='touch'] &:focus {
    box-shadow: none !important;
  }
}

.ctct-form-footer {
  bottom: 0 !important;
  display: inline-block !important;
  font: $font-weight--normal 1.3rem/2rem $font-family--sans !important;
  margin: 0 auto !important;
  max-width: 480px !important;
  padding: unit(0.5) 0 0 !important;
  position: absolute !important;
  right: 0 !important;
  text-align: right !important;
  z-index: 4 !important;

  @include respond-to('medium-large') {
    margin-left: 0 !important;
  }

  a {
    color: $color--white !important;
    pointer-events: all;
    text-decoration: none !important;

    @include hover {
      text-decoration: underline !important;
    }
  }
}

.ctct-form-errorMessage {
  background-color: $color--white !important;
  border-radius: 2px;
  bottom: unit(-1.7) !important;
  left: 0 !important;
  margin: 0 !important;
  padding: unit(0.2) !important;
  position: absolute !important;
  z-index: 10 !important;
}

.ctct-form-success {
  position: relative !important;
  z-index: 10 !important;
}
