/*
 * media queries
 * small:         30em
 * medium:        48.75em
 * medium-large:  61.25em
 * large:         75em
 * x-large:       87.5em
 */
.list-signup__container {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #045c78 url(..//images/list-signup.jpg) no-repeat center top;
  background-size: cover;
  min-height: 40rem;
  padding-bottom: 2rem;
  padding-top: 24rem;
  position: relative;
  text-align: center; }
  @media (min-resolution: 120dpi) and (max-width: 29.9375em) {
    .list-signup__container {
      background-image: url(..//images/list-signup@2x.jpg); } }
  @media (min-width: 30em) {
    .list-signup__container {
      background-image: url(..//images/list-signup--small.jpg); } }
  @media (min-resolution: 120dpi) and (min-width: 30em) {
    .list-signup__container {
      background-image: url(..//images/list-signup--small@2x.jpg); } }
  @media (min-width: 61.25em) {
    .list-signup__container {
      background-image: url(..//images/list-signup--medium.jpg);
      background-position: center;
      min-height: 25rem;
      padding-bottom: 4rem;
      padding-top: 4rem;
      text-align: left; } }
  @media (min-resolution: 120dpi) and (min-width: 61.25em) {
    .list-signup__container {
      background-image: url(..//images/list-signup--medium@2x.jpg); } }
  @media (min-width: 75em) {
    .list-signup__container {
      background-image: url(..//images/list-signup--large.jpg); } }
  @media (min-resolution: 120dpi) and (min-width: 75em) {
    .list-signup__container {
      background-image: url(..//images/list-signup--large@2x.jpg); } }
  .list-signup__container::after {
    content: "";
    display: block;
    position: absolute;
    background: linear-gradient(to top, #045c78 40%, rgba(4, 92, 120, 0) 100%);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1; }
    @media (min-width: 61.25em) {
      .list-signup__container::after {
        background: linear-gradient(to left, #045c78 40%, rgba(4, 92, 120, 0) 100%);
        left: 25%; } }

.list-signup {
  max-width: 480px;
  position: relative; }
  @media (max-width: 61.1875em) {
    .list-signup {
      margin: 0 auto; } }

.list-signup__newsletter {
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 2rem;
  margin-top: -2rem;
  position: relative;
  text-align: left;
  z-index: 3; }
  @media (min-width: 61.25em) {
    .list-signup__newsletter {
      margin-left: 0; } }
  .list-signup__newsletter a {
    text-decoration: none;
    color: #fff; }
    .list-signup__newsletter a:focus, .list-signup__newsletter a:hover {
      text-decoration: underline; }

/*
 * constant contact brute-force styles
 */
.g-recaptcha {
  display: none !important; }

.ctct-inline-form {
  background-color: transparent !important;
  margin: 0 auto !important;
  max-width: 480px !important;
  padding-bottom: 3rem !important;
  position: static !important;
  z-index: 3 !important; }

.ctct-form-defaults {
  background-color: transparent !important;
  padding: 0 !important; }

.ctct-form-header {
  color: #fff !important;
  font: 300 2.4rem/1.4 "Work Sans", sans-serif !important;
  margin-bottom: 1rem !important; }

.ctct-form-text,
.ctct-gdpr-text {
  color: #fff !important;
  font: 300 1.3rem/1.75 "Work Sans", sans-serif !important;
  margin: 1rem 0 !important; }

.ctct-gdpr-text {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem !important; }
  .ctct-gdpr-text a {
    border-bottom: 1px solid rgba(255, 255, 255, 0.5) !important;
    color: #fff !important;
    text-decoration: none !important; }

.ctct-form-custom {
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  padding-bottom: 5rem !important;
  position: relative !important;
  z-index: 3 !important; }
  @media (min-width: 61.25em) {
    .ctct-form-custom {
      margin-left: 0 !important; } }

.ctct-form-field {
  bottom: 0 !important;
  height: 4rem !important;
  margin: 0 !important;
  overflow: hidden !important;
  position: absolute !important;
  width: 100% !important; }

.ctct-form-label {
  color: #045c78 !important;
  font: 500 1.3rem/4rem "Work Sans", sans-serif !important;
  left: 1rem !important;
  line-height: 4rem !important;
  position: absolute !important;
  top: 0 !important;
  z-index: 3 !important; }
  .ctct-form-label::before {
    display: none !important; }

.ctct-form-custom[label-is='floated'] .ctct-form-label {
  top: -4rem !important; }

.ctct-form-element {
  appearance: none !important;
  background-color: #fff !important;
  border: none !important;
  border-radius: 2px !important;
  bottom: 0 !important;
  box-shadow: inset 1px 2px 3px rgba(0, 0, 0, 0.2) !important;
  color: #2f2f29 !important;
  font-size: 1.6rem !important;
  height: 4rem !important;
  left: 0 !important;
  margin: 0 !important;
  padding: 0 10rem 0 1rem !important;
  position: absolute !important;
  width: 100% !important; }

.ctct-form-button {
  background: transparent !important;
  border: none !important;
  border-radius: 0 !important;
  bottom: 1rem !important;
  color: #c25700 !important;
  font: 500 1.4rem/2rem "Work Sans", sans-serif !important;
  font-size: 1.4rem !important;
  padding: 0 14px 0 0 !important;
  position: absolute !important;
  right: 1rem !important;
  width: auto !important;
  z-index: 3 !important; }
  .ctct-form-button::after {
    content: "";
    display: block;
    position: absolute;
    background: url(..//images/arrow-link--orange.svg) no-repeat center;
    height: 12px;
    margin-top: -6px;
    right: 0;
    top: 50%;
    width: 7px; }

.ctct-form-element:focus,
.ctct-form-button:focus {
  box-shadow: 0 0 0 3px #f7a505 !important;
  outline: none !important; }

[data-whatintent='mouse'] .ctct-form-element:active,
[data-whatintent='mouse'] .ctct-form-element:focus,
[data-whatintent='touch'] .ctct-form-element:active,
[data-whatintent='touch'] .ctct-form-element:focus, [data-whatintent='mouse']
.ctct-form-button:active,
[data-whatintent='mouse']
.ctct-form-button:focus,
[data-whatintent='touch']
.ctct-form-button:active,
[data-whatintent='touch']
.ctct-form-button:focus {
  box-shadow: none !important; }

.ctct-form-footer {
  bottom: 0 !important;
  display: inline-block !important;
  font: 300 1.3rem/2rem "Work Sans", sans-serif !important;
  margin: 0 auto !important;
  max-width: 480px !important;
  padding: 1rem 0 0 !important;
  position: absolute !important;
  right: 0 !important;
  text-align: right !important;
  z-index: 4 !important; }
  @media (min-width: 61.25em) {
    .ctct-form-footer {
      margin-left: 0 !important; } }
  .ctct-form-footer a {
    color: #fff !important;
    pointer-events: all;
    text-decoration: none !important; }
    .ctct-form-footer a:focus, .ctct-form-footer a:hover {
      text-decoration: underline !important; }

.ctct-form-errorMessage {
  background-color: #fff !important;
  border-radius: 2px;
  bottom: -3.4rem !important;
  left: 0 !important;
  margin: 0 !important;
  padding: 0.4rem !important;
  position: absolute !important;
  z-index: 10 !important; }

.ctct-form-success {
  position: relative !important;
  z-index: 10 !important; }
